import React, { useState } from "react";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import Container from "react-bootstrap/Container";
import '../components/Navbar.css'
import { Link } from "react-router-dom";
import {
  AiOutlineApi,
  AiOutlineBarChart,
  AiOutlineHome,
  AiOutlineUser,
} from "react-icons/ai";
import { Dropdown } from "react-bootstrap";
import DropdownItem from "react-bootstrap/esm/DropdownItem";

function NavBar() {
  const [expand, updateExpanded] = useState(false);
  const [navColour, updateNavbar] = useState(false);

  function scrollHandler() {
    if (window.scrollY >= 20) {
      updateNavbar(true);
    } else {
      updateNavbar(false);
    }
  }

  const dropdown_style = {
    marginLeft: 10,
    background: "#1d1d1d",
  }

  const link_style = {
    color: "white",
  }

  window.addEventListener("scroll", scrollHandler);

  return (
    <Navbar
      expanded={expand}
      fixed="top"
      expand="md"
      className={navColour ? "sticky" : "navbar"}
    >
      <Container>
        <Navbar.Brand href="/" className="d-flex fs-4">
          ACM
        </Navbar.Brand>
        <Navbar.Toggle
          aria-controls="responsive-navbar-nav"
          onClick={() => {
            updateExpanded(expand ? false : "expanded");
          }}
        >
          {/* <span></span>
          <span></span>
          <span></span> */}
        </Navbar.Toggle>
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ms-auto fs-4" defaultActiveKey="#home">
            <Nav.Item>
              <Nav.Link as={Link} to="/" onClick={() => updateExpanded(false)}>
                <AiOutlineHome style={{ marginBottom: "2px" }} /> Home
              </Nav.Link>
            </Nav.Item>

            <Dropdown as={Nav.Item}>
              <Dropdown.Toggle as={Nav.Link} id="committees">
                <AiOutlineBarChart style={{ marginBottom: "2px" }} /> Committees
              </Dropdown.Toggle>
              <Dropdown.Menu style={dropdown_style}>
                {/* Dropdown items for committees */}
                <Dropdown.Item as={Link} style={link_style} to="/aippm">
                  AIPPM
                </Dropdown.Item>
                <Dropdown.Item as={Link} style={link_style} to="/g20">
                  G20
                </Dropdown.Item>
                <Dropdown.Item as={Link} style={link_style} to="/undp">
                  UNDP
                </Dropdown.Item>
                <Dropdown.Item as={Link} style={link_style} to="/unhrc">
                  UNHRC
                </Dropdown.Item>
                <Dropdown.Item as={Link} style={link_style} to="/unsc">
                  UNSC
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>


            <Nav.Item>
              <Nav.Link
                as={Link} style={link_style}
                to="/secretary"
                onClick={() => updateExpanded(false)}
              >
                <AiOutlineUser style={{ marginBottom: "2px" }} /> Secretary
              </Nav.Link>
            </Nav.Item>

          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default NavBar;
