import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import './Committee.css';
import Undp from '../../Assets/UNDP.png'
// import UnscSide from '../../Assets/Unsc-Side.png'


function Home() {


    const img_style = {
        height: 500,
    }

    return (
        <div className="Com-main">
            <section >
                <Container fluid className="home-section" id="home">
                    {/* <Particle /> */}
                    <Container className="home-content">

                        <Row>
                            <div className="com-outer-card">
                                <div className="com-card rounded d-flex justify-content-center align-items-center flex-column">
                                    <img src={Undp} alt="" className="com-img" style={img_style} />
                                    {/* <h1 className="p-3">G20</h1> */}
                                </div>
                                {/* <div className="side-img">
                                    <img src={UnscSide} alt="" />
                                </div> */}
                            </div>
                        </Row>

                    </Container>
                </Container>

            </section>

            <div className="committee-content d-flex justify-content-center align-items-center py-5">
                <div className="d-flex justify-content-center align-items-center flex-column rounded pb-5 custom">

                    <Row className="content">
                        The United Nations Development Programme (UNDP) is a United Nations agency tasked with helping countries eliminate poverty and achieve sustainable economic growth and human development. The UNDP emphasizes developing local capacity towards long-term self-sufficiency and prosperity. Based at United Nations Headquarters in New York City, it is the largest UN development aid agency, with offices in 177 countries. The UNDP is funded entirely by voluntary contributions from UN member states.


                    </Row>

                    <h1 className="pt-4 pb-2">Agenda</h1>

                    <Row className="content">
                        Ruminating over the UkraineEmergency.
                    </Row>

                </div>
            </div>
        </div>
    );
}

export default Home;
